.team-items{
  margin-top: 3.75em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
  gap: 1.875em;
  .team-card__img{
    border-radius: .625em;
  }
}


@media screen and (max-width: 1024px){
  .team-items{
    grid-template-columns: repeat(2,calc(50% - .9375em));
  }
}


@media screen and (max-width: 576px){
  .team-items{
    grid-template-columns: 100%;
    gap: .625em;
    margin-top: 2.5em;
  }
}
