@import "../vars.scss";
.page-top{
    padding: 1.875em 0;
    background-color: $light-bg;
}

.breadcrumbs {
    li{
        position: relative;
        display: inline;
        min-height: 1.25em;
        color: rgba($primary,.3);
        &:not(:first-child) {
            margin-left: 2em;
            a{
                &:before {
                    content: '/';
                    color: transparent;
                    position: relative;
                    z-index: 1;
                    top: .125em;
                    right: .875em;
                    margin-right: 0;
                    width: .45em;
                    height: .7em;
                    background-image: url(../img/bread-arrow.svg);
                    @include bgImgSize(100%);
                    background-position: center;
                    display: inline-flex;
                }
            }
        }
        &:last-child{
            pointer-events: none;
            color: $primary;
        }
    }
    a{
        font-size: 1.25em;
    }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
    .page-top{
        padding: 1.25em 0;
    }

    .breadcrumbs{
        font-size: .7em;
    }
}
