.products{
  &__img{
    margin-top: 3.75em;
    img{
      width: 100%;
      display: block;
    }
    ._view2{
      display: none;
    }
  }
  &__items{
    margin-top: 3.75em;
  }
}

.product-card{
  cursor: pointer;
  padding: 1.25em 0;
  border-bottom: $border;
  display: grid;
  grid-template-columns: 26.25em calc(100% - 33.75em) 3.75em;
  gap: 1.875em;
  transition: all .3s;
  &._open{
    padding: 2.5em 0;
    .product-card__col{
      padding-top: 0;
    }
    .product-card__btn{
      transform: rotate(90deg);
    }
  }
  &:first-child{
    border-top: $border;
  }
  &__col{
    transition: all .3s;
    padding-top: 1em;
  }
  &__col2{
    transition: all .3s;
  }
  &__content{
    display: none;
  }
  &__grid{
    display: grid;
    grid-template-columns: 24.375em calc(100% - 26.25em);
    gap: 1.875em;
  }
  &__img{
    img{
      width: 100%;
      display: block;
      border-radius: .625em;
    }
  }
}


@media screen and (max-width: 1024px){
  .product-card{
    grid-template-columns: 23em calc(100% - 26.75em - 3.75em) 3.75em;
    &__grid{
      grid-template-columns: 100%;
    }
    &__img{
      display: none;
    }
  }
}


@media screen and (max-width: 576px){
  .products{
    &__img{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .product-card{
    grid-template-columns: 100%;
    gap: 0;
    &._open{
      .product-card__col2{
        padding-top: 1.875em;
      }
    }
    &__img{
      display: block;
      img{
        min-height: 8.375em;
        object-fit: cover;
      }
    }
    &__col3{
      margin-top: 1.875em;
    }
  }
}
