.project-main{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 1.875em 2.5em;
  &__img{
    width: 100%;
    object-fit: cover;
    display: block;
    border-radius: .625em;
  }
  &__col2{
    padding: .875em 0;
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: auto;
    padding-top: 1.875em;
  }
  &__date{
    color: #BBC8E3;
  }
}

.project-gallery{
  margin-top: 2.5em;
  display: grid;
  grid-template-columns: repeat(4,calc(25% - 5.625em / 4));
  gap: 1.875em;
  &__item{
    @include imgCover(57%);
    border-radius: .625em;
  }
}

.project{
  overflow: hidden;
  &__group{
    margin-top: 3.125em;
    &--col2{
      .article{
        display: grid;
        grid-template-columns: repeat(2,calc(50% - 1.25em));
        gap: 2.5em;
        &--center{
          align-items: center;
        }
      }
      img{
        min-height: 100%;
        object-fit: cover;
      }
    }
  }
}

.article{
  &--space{
    p{
      margin-bottom: 2.5em;
      &:last-child{
        margin-bottom: 0;
      }
    }
  }
  img{
    width: 100%;
    display: block;
    border-radius: .625em;
  }
  blockquote{
    color: $primary;
    padding: 3.75em 2.5em 1.25em;
    position: relative;
    &:before,
    &:after{
      content: '';
      position: absolute;
      width: 3.4375em;
      height: 2.75em;
      @include bgImgSize(100%);
    }
    &:before{
      top: 0;
      left: 0;
      background-image: url(../img/quote.svg);
    }
    &:after{
      right: 0;
      bottom: 0;
      background-image: url(../img/quote2.svg);
    }
    p{
      font-style: italic;
      margin-bottom: 1.5em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    b{
      font-style: normal;
      font-weight: 600;
    }
  }
  h2,
  h3,
  h4{
    line-height: 90%;
    font-weight: 600;
  }
  h2{
    font-size: 4em;
  }
  h3{
    font-size: 2.5em;
  }
  h4{
    font-size: 2em;
  }
}

.project-gallery2{
  margin: 6.25em auto 0;
  width: 49.375em;
  min-height: 31.125em;
  max-width: 100%;
  &__img{
    transition: all .3s;
    @include imgCover(97%);
    padding: 0;
    height: 31.125em;
    border-radius: .625em;
    &:after{
      opacity: 0;
    }
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      padding-right: 1.875em;
      &:not(.swiper-slide-active){
        .project-gallery2__img{
          &:after{
            opacity: 1;
          }
        }
        @media screen and (min-width: 1025px){
          .project-gallery2__img{
            height: 24em;
          }
        }
      }
    }
  }
}

.project-info{
  margin-top: 6.25em;
  h2,
  h3{
    &:after{
      content: '';
      display: block;
      margin: 1em 0 .6em;
      width: 1.5em;
      height: 1.5px;
      background-color: $primary;
    }
  }
  h2{
    &:after{
      margin-top: 1.34em;
      margin-bottom: .78em;
    }
  }
  p{
    margin-bottom: 2.5em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  .col2,
  .project__group{
    margin-bottom: 3.125em;
    &:last-child{
      margin-bottom: 0;
    }
  }
  .col2{
    column-count: 2;
    column-gap: 3.125em;
  }
}

.project-nav{
  margin-top: 3.125em;
  display: flex;
  justify-content: space-between;
}

.project-link{
  padding: .875em 1.25em;
  background-color: $light-bg;
  display: flex;
  align-items: center;
  border-radius: .625em;
  color: $primary;
  &:hover{
    background-color: $primary;
    color: $white;
    path{
      fill: $white;
    }
  }
  &__desc{
    opacity: .4;
    font-size: .75em;
    line-height: 167%;
    font-weight: 500;
    text-transform: uppercase;
  }
  &__title{
    font-size: 1.25em;
    font-weight: 600;
    margin-top: .7em;
  }
  svg{
    width: .4375em;
    margin-left: 1.25em;
  }
  &--prev{
    text-align: right;
    svg{
      order: -1;
      margin: 0 1.25em 0 0;
    }
  }
}


@media screen and (max-width: 1024px){
  .project-main{
    &__img{
      min-height: 29em;
    }
    .h3{
      font-size: 2.75em;
    }
  }

  .project-gallery{
    &__item{
      padding-bottom: 117.6%;
    }
  }
}


@media screen and (max-width: 576px){
  .project-main{
    grid-template-columns: 100%;
    &__col2{
      padding: 0;
    }
    &__img{
      min-height: unset;
    }
  }

  .project-gallery{
    display: flex;
    gap: unset;
    width: calc(100% + 2em);
    margin-left: -1em;
    padding: 0 1em;
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    &__item{
      width: 14.5em;
      height: 9.75em;
      margin-right: 1.25em;
      padding: 0;
      flex-shrink: 0;
      &:last-child{
        margin-right: 0;
      }
    }
  }

  .project{
    &__group{
      margin-top: 2.5em;
      &--col2{
        .article{
          grid-template-columns: 100%;
        }
      }
    }
  }

  .article{
    &--space{
      p{
        margin-bottom: 3.125em;
      }
    }
    h2,
    h3{
      font-size: 1.75em;
    }
    h4{
      font-size: 1.5em;
    }
    blockquote{
      p{
        margin-bottom: 1.875em;
      }
    }
  }

  .project-gallery2{
    margin-top: 2.5em;
    min-height: unset;
    &__img{
      height: 24em;
      &:after{
        display: none;
      }
    }
    .swiper {
      &-container {
        width: calc(100% + 1.15em);
      }
      &-slide{
        padding-right: 1.25em;
      }
    }
  }

  .project-info{
    margin-top: 3.75em;
    h2,
    h3{
      &:after{
        margin: 1.43em 0;
        width: 2.15em;
      }
    }
    p{
      margin-bottom: 3.125em;
    }
    .col2{
      column-count: 1;
    }
  }

  .project-nav{
    @include grid100gap(.625em);
  }

  .project-link{
    justify-content: space-between;
    &--prev{
      order: 2;
    }
  }
}
