.news-main{
  display: grid;
  grid-template-columns: calc(100% - 50.375em) 48.125em;
  gap: 2.5em;
  &__col{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__items{
    margin-top: 10.25em;
    padding-top: 3.125em;
    position: relative;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.25em));
    gap: 2.5em;
    align-items: flex-start;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 3.75em;
      height: 1.5px;
      background-color: $primary;
      display: block;
    }
  }
}

.news-info{
  margin-top: 10.25em;
  @include grid100gap(3.75em);
  &__top{
    display: flex;
    align-items: center;
    &-col{
      &:not(:first-child){
        margin-left: 1.25em;
        padding-left: 1.25em;
        border-left: $border;
      }
    }
  }
  &__title{
    font-size: 3em;
    font-weight: 600;
  }
}

.news-item{
  @include grid100gap(1.875em);
}

.news-card{
  padding: 1.875em 0;
  border-top: $border;
  border-bottom: $border;
  display: grid;
  align-items: center;
  grid-template-columns: 9.875em calc(100% - 11.75em);
  gap: 1.25em 1.875em;
  &:hover{
    color: $black;
    .news-card__title{
      color: $primary;
    }
  }
  &:not(:last-child){
    border-bottom: none;
  }
  &__img{
    @include imgCover(78.4%);
    border-radius: .3125em;
  }
  &__title{
    transition: all .3s;
  }
  &__date{
    white-space: nowrap;
    margin-left: .5em;
    opacity: .2;
  }
  &__bottom{
    margin-top: 1.75em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .round-btn{
    margin-left: 3.3125em;
    border-color: $primary;
    &:hover{
      background-color: $primary;
      path{
        stroke: $white;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .news-main{
    grid-template-columns: 100%;
    &__items{
      margin-top: 6.25em;
    }
  }

  .news-info{
    margin-top: 6.25em;
  }
}


@media screen and (max-width: 576px){
  .news-main{
    &__items{
      margin-top: 3.75em;
      grid-template-columns: 100%;
    }
  }

  .news-info{
    margin-top: 3.75em;
    gap: 1.875em;
    &__title{
      font-size: 2em;
    }
  }

  .news-item{
    gap: 1em;
  }

  .news-items{
    display: flex;
    padding: 0 1em;
    margin-left: -1em;
    width: calc(100% + 2em);
    overflow-x: auto;
    &::-webkit-scrollbar {
      width: 0;
      height: 0;
    }
    .news-card{
      width: 18.75em;
      margin-right: 1.25em;
      flex-shrink: 0;
      &:last-child{
        margin-right: 0;
      }
    }
  }

  .news-card{
    padding: 0;
    border: none;
    grid-template-columns: 100%;
    &__bottom{
      margin-top: 1.25em;
    }
    .round-btn{
      display: none;
    }
  }
}
