.innovation{
  &__top{
    @include grid100gap(3.75em);
  }
  &__desc{
    max-width: 44.5em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 50% 50%;
    margin-top: 3.75em;
  }
}

.innovation2{
  @media screen and (min-width: 1025px){
    .innovation__top{
      grid-template-columns: 50% 50%;
      gap: 2.5em 0;
    }
    .innovation__desc{
      &:before{
        content: '';
        width: 3.75em;
        height: 1.5px;
        background-color: $primary;
        margin-bottom: 3em;
        display: block;
      }
    }
  }
  .h1{
    span{
      display: block;
    }
  }
}

.innovation-card{
  border: $border;
  height: 27.25em;
  position: relative;
  display: block;
  color: $white;
  &:hover{
    color: $white;
  }
  &__img{
    border-radius: inherit;
    height: 100%;
    transition: all .3s;
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, .2),rgba(0, 0, 0, 0.6) 100%),
                linear-gradient(180.00deg, rgba(0, 0, 0, 0.6),rgba(0, 0, 0, .3) 100%);
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.4) 100%);
    }
  }
  &__content{
    position: absolute;
    padding: 3.125em;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__nav{
    margin-top: auto;
  }
  .arrow-link{
    color: $white;
    &__icon{
      border-color: $white;
      background-color: $white;
    }
    &:hover{
      .arrow-link__icon{
        border-color: $primary;
        background-color: $primary !important;
      }
    }
  }
  @media screen and (min-width: 1025px) {
    &:nth-child(even) {
      border-left: none;
    }
    &:nth-child(1){
      border-radius: .625em 0 0 .625em;
    }
    &:nth-child(2){
      border-radius: 0 .625em .625em 0;
    }
    &__img{
      opacity: 0;
    }
    &:hover{
      .innovation-card__img{
        opacity: 1;
      }
    }
    &:not(:hover){
      color: $black;
      .innovation-card__title{
        font-weight: 400;
      }
      .arrow-link{
        color: $primary;
        &__icon{
          border-color: $primary;
          background-color: transparent;
        }
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .innovation{
    .h1{
      span{
        display: block;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .innovation{
    &__top{
      gap: 2.5em;
    }
    &__desc{
      max-width: 44.5em;
    }
    &__grid{
      margin-top: 2.5em;
      grid-template-columns: 100%;
    }
  }

  .innovation-card{
    height: 25em;
    &:nth-child(1){
      border-radius: .625em .625em 0 0;
    }
    &:last-child{
      border-radius: 0 0 .625em .625em;
    }
    &__content{
      padding: 1.875em;
    }
  }
}
