.news-page{
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 36.8125em - 2.5em) 36.8125em;
    gap: 2.5em;
  }
  &__content{
    margin-top: 2.5em;
    .project__group:first-child{
      margin-top: 0;
    }
  }
}

.news-main2{
  @include grid100gap(2.5em);
  &__img{
    width: 100%;
    display: block;
    border-radius: .625em;
  }
  .news-info__top{
    margin: 0;
  }
}

.news-sidebar{
  position: sticky;
  top: 1em;
}


@media screen and (max-width: 1024px){
  .news-page{
    &__grid{
      grid-template-columns: 100%;
    }
    &__col2{
      display: none;
    }
  }
}


