.modal-bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: #000;
    opacity: .25;
}

.modal {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: calc(100 * var(--vh));
    overflow-y: auto;
    z-index: 80;
    opacity: 0;
    pointer-events: none;
    //transition: all .3s;
    padding: 1em 0;
    text-align: center;

    &:after {
        content: '';
        display: inline-block;
        height: 100%;
        vertical-align: middle;
        margin-right: -4px;
    }
    &__dialog {
        text-align: left;
        vertical-align: middle;
    }

    &._active {
        opacity: 1;
        pointer-events: unset;
        .modal__bg {
            display: block;
        }
    }
    &__dialog {
        background: #fff;
        width: 40.75em;
        max-width: calc(100% - 2em);
        position: relative;
        z-index: 3;
        padding: 2.5em;
        border-radius: .625em;
        display: inline-block;
    }
    &__top{
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    .close-btn{
        margin-left: 1em;
        flex-shrink: 0;
    }
}

.close-btn{
    @include center;
    background-color: rgba($primary,.1);
    width: 3em;
    height: 3em;
    border-radius: .3125em;
    &:hover{
        background-color: $primary;
        path{
            stroke: $white;
        }
    }
    svg{
        width: 1.125em;
    }
}

.modal-form{
    margin-top: 2.5em;
    &__fields{
        @include grid100gap(.875em);
    }
    &__nav{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin-top: 2.5em;
    }
    &__hint{
        margin-top: 1em;
        font-size: 1em;
        line-height: 120%;
        opacity: .3;
    }
}


@media screen and (max-width: 992px){
    .modal{
        &__dialog {

        }
    }
}


@media screen and (max-width: 576px){
    .modal{
        &__dialog {
            padding: 1.25em;
        }
    }

    .modal-form{
        margin-top: 1.25em;
        &__fields{
            gap: .625em;
        }
        &__nav{
            margin-top: 1.25em;
        }
    }
}
