.main-page,
.sticky-menu{
  .burger{
    background: rgba($white,.1);
    path{
      stroke: $white;
    }
  }
}

.main-page{
  .header{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    border-bottom: 1px solid rgba($white,.5);
    &__logo{
      path:not(._not-fill){
        fill: $white;
      }
    }
    &__email{
      color: rgba($white,.5);
      &:hover{
        color: $white;
      }
    }
  }
}

.last-news{
  &__grid{
    display: grid;
    grid-template-columns: 48.125em calc(100% - 48.125em);
    margin-top: 3.75em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 1.875em;
  }
  @media screen and (min-width: 1025px){
    .news-card,
    .last-news__nav{
      padding-left: 3.75em;
    }
    .news-card2__img{
      min-height: 51.625em;
    }
    .news-card__img{
      padding-bottom: 78%;
    }
  }
}

.main-banner{
  position: relative;
  color: $white;
  &__img{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    &:after{
      background: rgba(0, 0, 0, 0.4) linear-gradient(180.00deg, rgba(0, 0, 0, 0.45),rgba(0, 0, 0, 0.15) 57%,rgba(0, 0, 0, 0.15) 100%);
    }
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
    }
  }
  &__inner{
    position: relative;
    z-index: 2;
    min-height: 56.25em;
    padding: 11.75em 0 3.75em;
    display: flex;
    flex-direction: column;
  }
  &__top{
    max-width: 58.75em;
  }
  &__title{
    font-size: 9.75em;
    line-height: 90%;
    font-weight: 600;
  }
  &__bottom{
    margin-top: auto;
    max-width: 32em;
    padding-top: 3em;
  }
  &__nav{
    margin-top: 2.5em;
  }
}


@media screen and (max-width: 1024px){
  .last-news{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .main-banner{
    &__top{
      max-width: 37.5em;
    }
    &__title{
      font-size: 6em;
    }
  }
}


@media screen and (max-width: 576px){
  .last-news{
    &__grid{
      gap: 2.5em;
      margin-top: 2.5em;
    }
    &__nav{
      padding-top: 2.5em;
    }
  }

  .main-banner{
    &__inner{
      padding-top: 11em;
      min-height: $vh;
    }
    &__title{
      font-size: 3.25em;
    }
  }
}
