.news-group4,
.news-group3,
.news-group2,
.news-group{
  margin-top: 6.25em;
  &:first-child{
    margin-top: 0;
  }
}

.news-group{
  display: grid;
  grid-template-columns: calc(100% - 48.125em) 48.125em;
  @media screen and (min-width: 1025px){
    .news-card{
      padding-right: 3.75em;
    }
  }
}

.news-card2{
  position: relative;
  color: $white;
  border-radius: .625em;
  &:hover{
    color: $white;
  }
  &__img{
    @include imgCover(95.5%);
    border-radius: .625em;
    overflow: hidden;
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, 0.6),rgba(0, 0, 0, 0.12) 50%,rgba(0, 0, 0, 0.6) 100%);
    }
  }
  &__content{
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    padding: 3.125em;
  }
  &__top{
    display: flex;
  }
  &__desc{
    margin-top: 1.875em;
  }
  &__badge{
    margin-left: 3.125em;
    flex-shrink: 0;
  }
  &__nav{
    display: flex;
    margin-top: auto;
    padding-top: 2.5em;
  }
}

.news-group4,
.news-group2{
  @media screen and (min-width: 1025px){
    .news-card2{
      &__img{
        padding: 0;
        height: 42.75em;
      }
    }
  }
  @media screen and (min-width: 577px){
    .news-card2{
      &__content{
        width: 33.625em;
        height: auto;
        padding: 1.875em;
        background-color: $white;
        color: $black;
        border-radius: .3125em;
        top: 3.125em;
        left: 3.125em;
      }
      .arrow-link{
        color: $primary;
        .arrow-link__icon{
          border-color: $primary;
          background-color: transparent;
          path{
            stroke: $primary;
          }
        }
        &:hover{
          background-color: $primary;
          color: $white;
          path{
            stroke: $white;
          }
        }
      }
    }
  }
  .news-card2{
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__badge{
      margin: 0 0 2.5em;
      background-color: $primary;
      color: $white;
      order: -1;
    }
  }
}

.news-group3{
  display: grid;
  grid-template-columns: 48.125em calc(100% - 50em);
  gap: 1.875em;
}

.news-items2{
  @media screen and (min-width: 577px){
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .9375em));
    gap: 1.875em;
    .news-card{
      padding: 0;
      border: none;
      grid-template-columns: 100%;
      gap: 1.25em;
      &__img{
        padding-bottom: 52.2%;
      }
      .round-btn{
        display: none;
      }
      &__top{
        flex-direction: column;
        align-items: flex-start;
      }
      &__date{
        order: -1;
        margin: 0 0 1em;
      }
      &__desc{
        display: none;
      }
    }
  }
}

.news-group4{
  display: grid;
  grid-template-columns: calc(100% - 50em) 48.125em;
  gap: 1.875em;
  .news-items2{
    margin-top: 1.875em;
  }
  @media screen and (min-width: 1025px){
    .news-card2{
      &__img{
        height: 46em;
      }
    }
  }
}


@media screen and (max-width: 1024px){
  .news-group4,
  .news-group3,
  .news-group{
    grid-template-columns: 100%;
    gap: 6.25em;
  }

  .news-card2{
    &__img{
      padding-bottom: 73.4%;
    }
  }
}


@media screen and (max-width: 576px){
  .news-group3,
  .news-group{
    gap: 2.5em;
    margin-top: 2.5em;
  }

  .news-card2{
    &__img{
      padding: 0;
      height: 48.75em;
    }
    &__content{
      padding: 1em;
    }
    &__top{
      flex-direction: column;
      align-items: flex-start;
    }
    &__badge{
      margin: 0 0 1.25em;
      order: -1;
    }
    &__desc{
      margin-top: 1.25em;
    }
  }

  .news-group4,
  .news-group2{
    margin-top: 2.5em;
    .news-card2{
      &__content{
        padding: 1em;
      }
      &__top{
        background-color: $white;
        padding: 1.875em;
        border-radius: .3125em;
        color: $black;
      }
      &__badge{
        margin-bottom: 1.25em;
      }
    }
  }

  .news-group4{
    .news-items2{
      display: none;
    }
  }
}
