.benefit{
  &__grid{
    margin-top: 3.75em;
  }
  &__img{
    width: 100%;
    min-height: 40em;
    object-fit: cover;
    display: block;
    border-radius: .625em;
  }
}

.benefit-card{
  padding: 2.25em 0;
  border-top: $border;
  cursor: pointer;
  &:last-child{
    border-bottom: $border;
  }
  &__grid{
    display: flex;
    align-items: flex-start;
  }
  &__icon{
    width: 2.875em;
    flex-shrink: 0;
    margin-right: 1.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__hidden{
    margin-top: 1.75em;
    display: none;
  }
  &__desc{
    max-width: 38em;
  }
  &:not(._active){
    .benefit-card__icon{
      margin-right: 0;
      width: 0;
    }
  }
}

.map{
  height: 49em;
  .ymaps-2-1-79-map{
    filter: grayscale(1);
  }
  .ymaps-2-1-79-balloon{
    font-size: 1em;
    font-family: $font1;
    box-shadow: none;
    padding: 0;
    max-width: calc(100vw - 5em);
    width: 37em;
    &__tail{
      box-shadow: none;
      background: unset;
      &:after{
        display: none;
      }
    }
    &__layout{
      box-shadow: 0 .25em 1.0625em 0 rgba(0, 0, 0, 0.21);
      border-radius: .625em;
      padding: 0;
    }
    &__content{
      font-family: $font1;
      font-size: 1em;
      padding: 0;
      min-width: unset;
      &>*{
        width: unset !important;
        height: unset !important;
        overflow: unset !important;
      }
    }
    &__close-button{
      width: 1.125em;
      height: 1.125em;
      background-image: url(../img/close2.svg);
      @include bgImgSize(100%);
      opacity: 1;
      margin: .9375em .9375em 0 0;
    }
  }
}

.place-card{
  background-size: cover;
  background-position: center;
  color: $white;
  border-radius: .625em;
  padding: 2em 1.875em;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  width: calc(100% + 30px);
  *{
    position: relative;
    z-index: 2;
  }
  &__badge{
    margin-bottom: 4.625em;
  }
  &__bottom{
    display: flex;
    align-items: flex-end;
    margin-top: 1.25em;
  }
  .round-btn{
    margin-left: 2.5em;
    flex-shrink: 0;
    background-color: $white;
    border-color: $white;
    path{
      stroke: $primary;
    }
  }
}


@media screen and (max-width: 1024px){
  .benefit {
    &__grid {
      gap: 0;
    }
    &__img{
      min-height: unset;
    }
  }
}


@media screen and (max-width: 576px){
  .map{
    height: 29em;
  }

  .place-card{
    padding: 1em;
    font-size: .875em;
    &__bottom{
      flex-direction: column;
      align-items: flex-start;
    }
    .round-btn{
      margin: 1em 0 0;
    }
  }

  .benefit{
    .h1{
      word-break: break-word;
    }
  }
}
