.contacts{
  background-color: $light-bg;
  &__col{
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    margin-top: auto;
    padding-top: 3.5em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
  }
}

.contacts2{
  @media screen and (min-width: 1025px){
    .map{
      height: 51.25em;
    }
  }
}

.contact-item{
  padding: 2.25em 0;
  border-top: $border;
  @include grid100gap(.625em);
  &:last-child{
    border-bottom: $border;
  }
  a{
    &:hover{
      color: $black;
    }
  }
}

.map{
  height: 36.5em;
  border-radius: .625em;
  overflow: hidden;
}

.works{
  &__grid{
    display: flex;
    margin-top: 3.5em;
    &:first-child{
      margin-top: 0;
    }
  }
}

.work-card{
  width: 25%;
  transition: all .3s;
  height: 35.5em;
  border: $border;
  position: relative;
  cursor: pointer;
  &__img{
    border-radius: inherit;
    height: 100%;
    transition: all .3s;
    opacity: 0;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.4) 100%);
    }
  }
  &__num{
    position: absolute;
    top: 3.125em;
    left: 3.125em;
    color: #E7EBF2;
    font-weight: 200;
    span{
      font-size: 6em;
      line-height: 90%;
    }
  }
  &__content{
    position: absolute;
    left: 0;
    bottom: 0;
    width: 100%;
    padding: 2.5em 3.125em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    overflow: hidden;
  }
  &__col{
    max-width: 37.875em;
    &:before{
      content: '';
      width: 3.75em;
      height: 1.5px;
      background-color: $white;
      margin-bottom: 1.875em;
      display: block;
    }
  }
  .round-btn{
    border-color: $primary;
    margin-left: 1em;
    &:hover{
      border-color: $primary !important;
      background-color: $primary;
      path{
        stroke: $white;
      }
    }
  }
  &__title{
    min-height: 1.875em;
    display: flex;
    align-items: center;
  }
  &__desc{
    height: 0;
    opacity: 0;
    transition: all .3s;
  }
  @media screen and (min-width: 1025px){
    &:nth-child(3n),
    &:nth-child(3n-1){
      border-left: none;
    }
    &._active{
      width: 50%;
      color: $white;
      .work-card__num{
        opacity: 0;
      }
      .work-card__img{
        opacity: 1;
      }
      .round-btn{
        border-color: $white;
      }
      .h2{
        font-weight: 600;
      }
      .work-card__desc{
        height: auto;
        opacity: 1;
        margin-top: 1.875em;
      }
    }
    &:nth-child(1){
      border-radius: .625em 0 0 .625em;
    }
    &:nth-child(3){
      border-radius: 0 .625em .625em 0;
    }
  }
}

.about-main{
  &__img{
    border-radius: .625em;
    display: block;
    width: 100%;
  }
}


@media screen and (max-width: 1024px){
  .map{
    height: 21em;
  }

  .works{
    &__grid{
      display: block;
    }
  }

  .work-card{
    width: 100%;
    color: $white;
    &:nth-child(1){
      border-radius: .625em .625em 0 0;
    }
    &:last-child{
      border-radius: 0 0 .625em .625em;
    }
    &:not(:first-child){
      border-top: none;
    }
    &__img{
      opacity: 1;
    }
    &__num{
      opacity: 0;
    }
    .round-btn{
      border-color: $white;
    }
    .h2{
      font-weight: 600;
    }
    &__desc{
      height: auto;
      opacity: 1;
      margin-top: 1.875em;
    }
  }
}


@media screen and (max-width: 576px){
  .contacts{
    &__bottom{
      padding-top: 2.5em;
    }
  }

  .contact-item{
    padding: 1.625em 0;
    &__desc{
      font-size: 1.25em;
    }
  }

  .map{
    height: 18.75em;
  }

  .works{
    &__grid{
      margin-top: 2.5em;
    }
  }

  .work-card{
    height: 31.25em;
    &__content{
      padding: 1.875em;
      flex-direction: column;
      align-items: flex-start;
    }
    .round-btn{
      margin: 1.75em 0 0;
    }
    &__desc{
      margin-top: 1.75em;
    }
  }

  .about-main{
    padding-top: 2.5em;
  }
}
