@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 1.875em;
    height: 3.75em;
    border-radius: 62.5em;
    font-weight: 500;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: 1.25em;
    }
}

.m-btn-blue-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.m-btn-light-blue{
    background-color: $light-stroke;
    color: $primary;
    &:hover{
        background-color: $primary;
        color: $white;
    }
}

.blue-link{
    color: $primary;
    &:hover{
        color: $black;
    }
}

.arrow-link{
    display: flex;
    align-items: center;
    font-weight: 500;
    color: $primary;
    border-radius: 62.5em;
    &:hover{
        padding-right: .625em;
        background-color: $primary;
        color: $white;
        path{
            stroke: $white;
        }
        .arrow-link__title{
            transform: translateX(-.7em);
        }
    }
    &--white{
        border: 1px solid transparent;
        color: $white;
        .arrow-link__icon{
            border-color: $white;
            background-color: $white;
            path{
                stroke: $primary;
            }
        }
        &:hover{
            background-color: transparent;
            border-color: $white;
            .arrow-link__icon{
                background-color: transparent;
                border-color: transparent;
                path{
                    stroke: $white;
                }
            }
        }
    }
    &__title{
        font-size: 1.25em;
        transition: all .3s;
    }
    &__icon{
        margin-right: .875em;
        border: 1.50px solid $primary;
        width: 3.75em;
        height: 3.75em;
        @include center;
        border-radius: 50%;
        svg{
            width: 1.1875em;
        }
    }
}

.round-btn{
    flex-shrink: 0;
    width: 3.75em;
    height: 3.75em;
    @include center;
    background-color: $white;
    border: 1px solid $white;
    border-radius: 50%;
    svg{
        width: 1.1875em;
    }
    &:hover{
        background-color: transparent;
        border-color: $primary;
    }
    &:active{
        background-color: $primary;
        border-color: $primary;
        path{
            stroke: $white;
        }
    }
    &--blue{
        border-color: $primary;
        &:hover{
            background-color: $primary;
            border-color: $primary;
            path{
                stroke: $white;
            }
        }
    }
}
