.search-field{
  margin-top: 3.75em;
  position: relative;
  &__icon{
    position: absolute;
    left: 1.875em;
    top: 50%;
    transform: translateY(-50%);
    width: 1.3125em;
  }
  input{
    height: 3em;
    padding-left: 3.25em;
    border-radius: 5em;
    background-color: $light-bg;
    border-color: $light-bg;
  }
}

.search{
  &__items{
    margin-top: 2.5em;
  }
}

.search-item{
  padding: 2.5em 0;
  border-top: $border;
  @include grid100gap(1.5em);
  &:last-child{
    border-bottom: $border;
  }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
  .search-field{
    margin-top: 2.5em;
    &__icon{
      width: 1.125em;
    }
    input{
      height: 3.5em;
      padding-left: 3.625em;
      border-radius: 6.25em;
    }
  }

  .search-item{
    padding: 1.875em 0;
  }
}
