.header{
  padding: .875em 0;
  position: relative;
  z-index: 10;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    z-index: 21;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 4.6875em;
    position: relative;
    z-index: 21;
    svg,
    img{
      width: 100%;
      display: block;
    }
  }
  &__inner .burger,
  &__email{
    position: relative;
    z-index: 21;
  }
  &._open{
    .sticky-menu{
      background-color: $white;
      color: $primary;
      .lang{
        path{
          stroke: $primary;
        }
      }
      .burger{
        opacity: 0;
        pointer-events: none;
      }
      .sticky-menu__search{
        background-color: $light-stroke;
        ._view1{
          opacity: 0;
        }
        ._view2{
          opacity: 1;
        }
      }
    }
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .header__inner{
      .burger{
        background: rgba($white,.1);
        path{
          stroke: $white;
        }
      }
    }
    .header__logo{
      path:not(._not-fill){
        fill: $white;
      }
    }
    .burger,
    .sticky-menu__search{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.burger{
  width: 3em;
  height: 3em;
  @include center;
  border-radius: .5em;
  display: none;
  position: relative;
  z-index: 21;
  svg,
  img{
    width: 1.625em;
    @include centerAbsolute;
  }
  ._view2{
    width: 1.125em;
    opacity: 0;
  }
}

.sticky-menu{
  position: fixed;
  top: 0;
  left: 0;
  height: $vh;
  width: 5em;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  background-color: $primary;
  padding: 1.875em 0;
  transition: all .3s;
  z-index: 20;
  color: $white;
  &__search{
    width: 3em;
    height: 3em;
    @include center;
    position: relative;
    border-radius: .5em;
    &:hover{
      background-color: $black;
    }
    img{
      transition: all .3s;
      width: 1.375em;
      @include centerAbsolute;
    }
    ._view2{
      opacity: 0;
      width: 1.125em;
    }
  }
  .burger{
    display: flex;
    &:hover{
      background-color: $black;
    }
  }
}

.lang{
  display: flex;
  align-items: center;
  &:hover{
    color: inherit;
  }
  span{
    font-size: .875em;
  }
  svg{
    width: 1.375em;
    margin-right: .3125em;
  }
}

.menu{
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: $vh;
  background-color: $primary;
  z-index: 19;
  opacity: 0;
  pointer-events: none;
  transition: all .3s;
  padding-top: 6.875em;
  color: $white;
  &__wrap{
    height: 100%;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background: rgba($white,.5);
    }
  }
  &__inner{
    width: calc(100% + 5em);
    padding-right: 5em;
    overflow-y: auto;
    overflow-x: hidden;
    height: calc(100 * var(--vh) - 6.875em);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-top: 1.875em;
  }
  .search-field{
    margin-top: 0;
  }
}

.nav{
  padding: 2.5em 0;
  ul{
    column-count: 2;
    column-gap: 1em;
  }
  li{
    display: inline-block;
    width: 100%;
    margin-bottom: 1.375em;
  }
  a{
    font-size: 2em;
    &:hover{
      color: $light-stroke;
    }
  }
}

.menu-contacts{
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 2.625em 0;
  position: relative;
  &:before{
    content: '';
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 200vw;
    height: 1px;
    background: rgba($white,.5);
  }
  &__item{
    &-title{
      opacity: .3;
      margin-bottom: .25em;
    }
    a:hover{
      color: $white;
      opacity: .8;
    }
  }
}


@media screen and (max-width: 1024px){
  .nav{
    li{
      margin-bottom: 1em;
    }
    a{
      font-size: 1.5em;
    }
  }

  .menu{
    &__inner{
      width: calc(100% + 4em);
      padding-right: 4em;
    }
  }

  .burger{
    &:hover{
      background-color: $light-stroke;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    padding: 1.25em 0;
    &__logo{
      width: 3.4375em;
    }
    &__email{
      display: none;
    }
  }

  .burger{
    display: flex;
  }

  .menu{
    padding-top: 6.25em;
    &__inner {
      width: calc(100% + 1em);
      padding-right: 1em;
      height: calc(100 * var(--vh) - 6.25em);
      justify-content: unset;
    }
  }

  .nav{
    ul{
      column-count: 1;
    }
    li{
      margin-bottom: .75em;
    }
  }

  .menu-contacts{
    @include grid100gap(1em);
    padding: 1.875em 0;
    margin-top: auto;
  }

  .sticky-menu{
    display: none;
  }
}
