.project-main2{
  margin-bottom: 6.25em;
  @media screen and (min-width: 577px){
    .m-section__grid{
      grid-template-columns: repeat(2,calc(50% - 1.25em));
      gap: 2.5em;
    }
  }
  &__img{
    width: 100%;
    display: block;
    border-radius: .625em;
  }
}

.project-gallery3{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9375em));
  gap: 1.875em;
  .project-gallery__item{
    padding-bottom: 67.7%;
  }
}

.warning{
  padding: 2.5em;
  color: #901616;
  background-color: rgba(#FF1010,.1);
  border: 1px solid rgba(#FF1010,.15);
  border-radius: .625em;
}


@media screen and (max-width: 1024px){
  .project-main2{
    @media screen and (min-width: 577px){
      .project-main2__img{
        min-height: 29em;
        object-fit: cover;
      }
    }
    .h1{
      font-size: 3em;
    }
  }

  .project-gallery3{
    .project-gallery__item{
      padding-bottom: 140%;
      width: 100%;
      height: unset;
      margin: 0;
    }
  }

}


@media screen and (max-width: 576px){
  .project-main2{
    margin-bottom: 5.375em;
    .h1{
      font-size: 2.75em;
    }
  }

  .warning{
    padding: 1.875em;
  }

  .project-gallery3{
    row-gap: 1.25em;
    .project-gallery__item{
      padding-bottom: 92%;
    }
  }
}
