.project-collection{
  margin-top: 6.25em;
}

.project-slider{
  margin-top: 3.75em;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.875em);
    }
    &-slide{
      padding-right: 1.875em;
      width: calc(100% / 3);
    }
  }
}

.project-card{
  position: relative;
  &__badge{
    position: absolute;
    top: 2em;
    left: 1.875em;
  }
  &__img{
    border-radius: .625em;
    @include imgCover(54.4%);
    &:after{
      background: rgba(0, 0, 0, .4);
    }
  }
  &__content{
    margin-top: 1.625em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__desc{
    margin-top: 1em;
  }
  .round-btn{
    background-color: $primary;
    border-color: $primary;
    path{
      stroke: $white;
    }
    &:hover{
      opacity: .8;
    }
  }
}

.project-top{
  &__info{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__desc{
    &:before{
      content: '';
      width: 3.75em;
      height: 1.5px;
      background-color: $primary;
      margin-bottom: 3em;
      display: block;
    }
  }
  .h1{
    ._view2{
      display: none;
    }
  }
  .partners__items{
    margin-top: 3.75em;
  }
  .map{
    margin-top: 3.75em;
  }
  @media screen and (min-width: 1025px){
    .partner-item{
      height: 15.75em;
    }
  }
}

.projects{
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1.25em));
    gap: 3.75em 1.875em;
  }
}

.more-nav{
  margin-top: 3.75em;
  display: flex;
  justify-content: center;
}

.project-top2{
  .project-top__desc:before{
    display: none;
  }
}


@media screen and (max-width: 1024px){
  .project-slider{
    .slider__wrap{
      overflow: unset;
    }
    .swiper{
      &-container{
        width: 100%;
      }
      &-slide{
        width: 35.625em;
      }
    }
  }

  .project-top {
    &__info {
      grid-template-columns: 100%;
      gap: 3.75em;
    }
    .h1{
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }

  .projects{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .9375em));
    }
  }
}


@media screen and (max-width: 576px){
  .project-collection{
    margin-top: 3.75em;
  }

  .project-slider{
    margin-top: 1.875em;
    .swiper{
      &-slide{
        width: calc(100% + 1.25em);
        padding-right: 1.25em;
      }
    }
  }

  .project-card{
    &__img{
      padding-bottom: 64%;
    }
    &__badge{
      top: 1.25em;
      left: 1.25em;
    }
    &__title{
      width: calc(100% + 2.14em);
    }
  }

  .project-top{
    &__info{
      gap: 2.5em;
    }
    &__desc{
      &:before{
        margin-bottom: 2.5em;
      }
    }
    .partners__items{
      margin-top: 2.5em;
    }
    .map{
      margin-top: 2.5em;
    }
  }

  .projects{
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .more-nav{
    margin-top: 2.5em;
    .m-btn{
      width: 100%;
    }
  }
}
