.conf{
  .article{
    margin-top: 3.75em;
    h4{
      margin-bottom: .75em;
      font-weight: 500;
    }
    p{
      margin-bottom: 1.25em;
      &:last-child{
        margin-bottom: 0;
      }
    }
    hr{
      margin: 2.5em 0;
      border-color: $light-stroke;
    }
  }
}


@media screen and (max-width: 1024px){

}


@media screen and (max-width: 576px){
  .conf{
    .h1{
      font-size: 1.75em;
    }
    .article{
      margin-top: 2.5em;
      h4{
        margin-bottom: 1em;
      }
      p{
        margin-bottom: 1.5em;
      }
    }
  }
}
