.project-main2,
.about-main,
.consult,
.partners{
  &__col{
    max-width: 44em;
    display: flex;
    flex-direction: column;
  }
  &__bottom{
    margin-top: auto;
    &:before{
      content: '';
      width: 3.75em;
      height: 1.5px;
      background-color: $primary;
      margin: 3.75em 0;
      display: block;
    }
  }
}

.partners{
  &__nav{
    margin-top: 3em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3));
  }
}

.partner-item{
  height: 10.75em;
  padding: 1.875em;
  @include center;
  border-right: 1px solid $light-stroke;
  border-top: 1px solid $light-stroke;
  img{
    max-width: 100%;
    max-height: 100%;
    transition: all .3s;
    opacity: .5;
  }
  &:hover{
    img{
      opacity: 1;
    }
  }
  @media screen and (min-width: 577px){
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(3){
      border-top: none;
    }
    &:nth-child(3n){
      border-right: none;
    }
  }
}

.consult{
  .h1{
    max-width: 5.5em;
  }
}

.consult-form{
  &__fields{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__nav{
    margin-top: 1.25em;
  }
  &__hint{
    text-align: right;
    max-width: 27em;
    font-size: 1em;
    line-height: 120%;
    opacity: .3;
  }
}

._full{
  grid-column: 1/-1;
}


@media screen and (max-width: 1024px){
  .partner-item{
    padding: 1.875em .5em;
  }

  .consult{
    @media screen and (min-width: 577px){
      .h1{
        max-width: unset;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .project-main2,
  .about-main,
  .partners,
  .consult{
    &__bottom {
      &:before {
        margin: 2.5em 0;
      }
    }
  }

  .partners{
    &__nav{
      margin-top: 2.5em;
    }
    &__items{
      grid-template-columns: 50% 50%;
      .partner-item:nth-child(4)~.partner-item{
        display: none;
      }
    }
  }

  .partner-item{
    height: 9em;
    padding: 1.875em;
    &:nth-child(1),
    &:nth-child(2){
      border-top: none;
    }
    &:nth-child(2n){
      border-right: none;
    }
  }

  .consult-form{
    &__fields{
      grid-template-columns: 100%;
      gap: .625em;
    }
    &__nav{
      flex-direction: column;
      align-items: flex-start;
    }
    &__hint{
      margin: 1em 0 0;
      text-align: left;
    }
  }
}
