.vacancies-items{
  margin-top: 3.75em;
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .9375em));
  gap: 1.875em;
}

.vacancy-card{
  padding: 2em 1.875em;
  border: $border;
  border-radius: .625em;
  display: flex;
  flex-direction: column;
  &__top{
    padding-bottom: 1.875em;
    border-bottom: $border;
  }
  &__info{
    @include grid100gap(.5em);
  }
  .socials{
    margin-left: 1em;
  }
  &__badges{
    margin-top: 1.25em;
    display: flex;
    flex-wrap: wrap;
    .m-badge{
      margin: .625em .625em 0 0;
    }
  }
  &__desc{
    margin-top: 1.875em;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.875em;
    display: flex;
  }
}

.vacancy-modal{
  .modal__dialog{
    width: 61.375em;
  }
}

.file-input{
  display: flex;
  align-items: center;
  &__btn{
    position: relative;
    height: 4.625em;
    padding: 0 1.875em;
    border-radius: .3125em;
    overflow: hidden;
    input{
      cursor: pointer;
      position: absolute;
      right: 0;
      top: 0;
      width: 600px;
      height: 600px;
      opacity: 0;
    }
  }
  &__uploaded{
    opacity: .5;
    margin-left: 1.25em;
  }
}


@media screen and (max-width: 1024px){
  .vacancies-items{
    grid-template-columns: 100%;
  }
}


@media screen and (max-width: 576px){
  .vacancies-items{
    gap: .625em;
  }

  .vacancy-card{
    &__badges{
      flex-wrap: nowrap;
      overflow-x: auto;
      width: calc(100% + 3.75em);
      margin-left: -1.875em;
      padding: 0 1.875em;
      @include nullScroll;
      .m-badge{
        flex-shrink: 0;
      }
    }
    .socials{
      flex-direction: column;
      &__item2,
      &__item{
        margin: 0 0 .625em;
      }
    }
  }

  .file-input{
    flex-direction: column;
    &__btn{
      width: 100%;
      height: 2.875em;
      span{
        font-size: 1em;
      }
    }
    &__uploaded{
      margin: .875em 0 0;
    }
  }
}
