.team{
  background-color: $light-bg;
  &__wrap{
    margin-top: 3.75em;
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__desc{
    max-width: 44.5em;
  }
}

.team-slider{
  margin-top: 3.5em;
  .slider__wrap{
    overflow: hidden;
  }
  .swiper{
    &-slide{
      width: 25%;
      &:first-child{
        .team-card__content{
          border-radius: 0 0 0 .625em;
        }
        .team-card__img{
          border-radius: .625em 0 0 .625em;
        }
      }
      &:last-child{
        .team-card__content{
          border-radius: 0 0 .625em 0;
        }
        .team-card__img{
          border-radius: 0 .625em .625em 0;
        }
      }
    }
  }
}

.m-badge{
  padding: 0 1.5em;
  height: 2.875em;
  display: flex;
  align-items: center;
  background-color: $white;
  color: $primary;
  border-radius: 6.25em;
  &._blue{
    background-color: $primary;
    color: $white;
  }
  &._light-blue{
    background-color: $light-stroke;
    color: $primary;
  }
}

.team-card{
  display: block;
  position: relative;
  color: $white;
  &:hover{
    color: $white;
    .team-card__content{
      background-color: $primary;
    }
    .round-btn{
      opacity: 1;
    }
  }
  &__img{
    @include imgCover(121%);
    &:after{
      background: linear-gradient(180.00deg, rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.4) 100%);
    }
  }
  &__content{
    transition: all .3s;
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    padding: 2em 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__info{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__prof{
    margin-top: 1.25em;
  }
  .round-btn{
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
}


@media screen and (max-width: 1024px){
  .team{
    overflow: hidden;
    &__wrap{
      flex-direction: column;
      align-items: flex-start;
    }
    .arrow-link{
      margin-top: 3.75em;
    }
  }

  .team-slider {
    .slider__wrap {
      overflow: unset;
    }
    .swiper-slide{
      width: 26.25em;
    }
  }
}


@media screen and (max-width: 576px){
  .team{
    &__wrap{
      margin-top: 2.5em;
    }
    .arrow-link{
      margin-top: 2.5em;
    }
  }

  .team-slider {
    margin-top: 2.5em;
    .swiper-slide{
      width: 19.125em;
    }
  }

  .team-card{
    &__content{
      padding: 1.25em;
    }
    .round-btn{
      display: none;
    }
  }

  .m-badge{
    height: 2.75em;
    span{
      font-size: 1em;
    }
  }
}
