.footer{
  background-color: $black;
  color: $white;
  &__logo{
    display: block;
    width: 9.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__title{
    font-size: 2.25em;
    line-height: 90%;
    opacity: .3;
    font-weight: 600;
  }
}

.footer-top{
  &__inner{
    display: grid;
    grid-template-columns: 50% 50%;
  }
  &__col{
    padding: 3.25em 0;
  }
  &__col2{
    padding: 3.125em;
    border-left: 1px solid #5a6072;
  }
}

.footer-contacts{
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - 1.5625em));
    gap: 1em 3.125em;
    align-items: flex-start;
    margin-top: 2.25em;
  }
  &__col{
    @include grid100gap(1em);
  }
  .menu-contacts__item-desc{
    font-weight: 600;
  }
}

.footer-middle{
  border-top: 1px solid #5a6072;
  &__inner{
    display: grid;
    grid-template-columns: 50% 25%;
  }
  &__col,
  &__col2{
    padding: 3.125em;
    padding-bottom: 2.25em;
  }
  &__col{
    padding-left: 0;
  }
  &__col2{
    border-left: 1px solid #5a6072;
    border-right: 1px solid #5a6072;
  }
}

.footer-nav{
  &__links{
    margin-top: 2.5em;
    &._col1{
      column-count: 1;
    }
    ul{
      column-count: 2;
      column-gap: 3.125em;
    }
    li{
      display: inline-block;
      width: 100%;
      margin-bottom: .875em;
    }
    a{
      font-size: 1.25em;
    }
  }
}

.footer-bottom{
  background: #3f4352;
  padding: 1.875em 0;
  color: rgba($white,.3);
  a:hover{
    color: $white;
  }
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    font-size: 1.25em;
  }
}


@media screen and (max-width: 1024px){
  .footer-top{
    &__inner{
      grid-template-columns: 100%;
    }
    &__col2{
      padding-left: 0;
      padding-right: 0;
      border-left: none;
    }
  }

  .footer-middle{
    &__inner{
      grid-template-columns: 50% 50%;
    }
    &__col2{
      border-right: none;
    }
  }

  .footer-nav{
    &__links{
      ul{
        column-count: 1;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .footer{
    &__logo{
      width: 6em;
    }
    &__title{
      font-size: 1.75em;
    }
  }

  .footer-top{
    &__col{
      padding: 3.125em 0 2.5em;
    }
    &__col2{
      padding: 2.5em 0;
      border-top: 1px solid #5a6072;
    }
  }

  .footer-contacts{
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.25em;
    }
    .menu-contacts__item{
      .text20{
        font-size: 1em;
      }
    }
  }

  .footer-middle{
    border: none;
    &__inner{
      grid-template-columns: 100%;
    }
    &__col,
    &__col2{
      padding: 2.5em 0 1.625em;
      border: none;
      border-top: 1px solid #5a6072;
    }
  }

  .footer-nav{
    &__links{
      margin-top: 1.25em;
      a{
        font-size: 1em;
      }
    }
  }

  .footer-bottom{
    &__inner{
      @include grid100gap(.5em);
    }
    &__item{
      font-size: 1em;
    }
  }
}
