@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import "components/_inputs.scss";
@import "components/_pagination.scss";
@import "components/_tabs.scss";
@import "components/_grid.scss";
._flex-center,
._flex-between,
._flex{
  display: flex;
  align-items: center;
}

._flex-between{
  justify-content: space-between;
}

._flex-center{
  justify-content: center;
}

.m-section{
  padding: 6.25em 0;
  &._blue{
    background-color: $light-bg;
  }
  &._white{
    background-color: $white;
  }
  &--first{
    padding-top: 3.75em;
  }
  &__grid{
    display: grid;
    grid-template-columns: 50% 50%;
    gap: 3.75em 0;
    justify-content: space-between;
  }
}

._light-bg{
  background-color: $light-bg;
}

.m-backdrop{
  position: relative;
  &:after{
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    pointer-events: none;
    border-radius: inherit;
  }
}

._scale-img{
  overflow: hidden;
  &:hover{
    img{
      transform: scale(1.1);
    }
  }
  img{
    transition: all .6s;
  }
}

.socials{
  display: flex;
  align-items: center;
  &__item2,
  &__item{
    width: 3.375em;
    height: 3.375em;
    border-radius: 50%;
    margin-right: .625em;
    @include center;
    &:last-child{
      margin: 0;
    }
    svg{
      width: 100%;
    }
  }
  &__item{
    &:hover{
      background-color: $primary;
      circle{
        fill: $white;
      }
      path{
        fill: $primary;
      }
    }
  }
  &__item2{
    &:hover{
      opacity: .75;
    }
  }
}

.compare-btn,
.favourite-btn{
  @include center;
  svg{
    width: 100%;
    height: auto;
  }
}

.favourite-btn{
  width: 1.4375em;
  &:hover{
    path{
      opacity: 1;
    }
  }
  &._added{
    path{
      opacity: 1;
      stroke: $primary;
      fill: $primary;
    }
  }
}

.compare-btn{
  width: 1.5em;
  &:hover{
    g{
      opacity: 1;
    }
  }
  &._added{
    g{
      opacity: 1;
    }
    line{
      stroke: $primary;
    }
  }
}




@media screen and (max-width: 1024px){
  .m-section{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .m-section{
    padding: 3.75em 0;
    &--first{
      padding-top: 2.5em;
    }
    &__grid{
      gap: 2.5em;
    }
  }

  ._hide-mob{
    display: none;
  }
}
